<template>
  <div id="TypeQuickly" class="pb-16">
    

    <div class="w-full flex flex-col items-center font-bold">
      <div class="w-full flex flex-col max-w-xl mt-3">

        <div class="w-full flex justify-center">
          <div class="p-3 w-full flex flex-wrap text-left">
            <span class="w-1/2">allLetterCount : {{allLetterCount}}</span>
            <span class="w-1/2">correctLetterCount : {{correctLetterCount}}</span>
            <span class="w-1/2">wrongLetterCount : {{wrongLetterCount}}</span>
            <span class="w-1/2">skinpLetterCount : {{skinpLetterCount}}</span>
          </div>
        </div>

        <div class="w-full flex justify-center">
          <div class="p-3 w-full h-64">
            <div class="h-full w-full flex flex-wrap items-center justify-center rounded-lg overflow-y-auto border text-2xl font-bold bg-gray-800" >
              <span v-for="(letter,i) in targetTextShow" :key="i" :class="{'px-1':letter.letter == ' '}" :style="'color :'+letter.color" v-text="letter.letter"/>
            </div>
          </div>
        </div>

        <div class="w-full flex justify-center">
          <div class="p-3 w-full h-20">
            <input type="text" :maxlength="targetText.length" v-model="userText" class="h-full w-full px-5 flex items-center justify-center rounded-lg overflow-hidden border text-2xl font-bold bg-gray-800 focus:outline-none" placeholder="write text here">
          </div>
        </div>


      </div>
    </div>
    

  </div>
</template>

<script>
export default {
  name: 'TypeQuickly',
  components: {},
  data() {
    return {
      targetText:"Musab mohammed abou alagsm hassan",
      userText:"",

      userTextShift:0,

      allLetterCount:0,
      correctLetterCount:0,
      wrongLetterCount:0,
      skinpLetterCount:0,

      targetTextWithColors:[],
      colors:{"Default":"black","Right":"green","Wrong":"red","Skipping":"blue"}
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      this.addColorText();
    },
    addColorText(){
      var rrr = [];
      for (let i = 0; i < this.targetText.split(" ").length; i++) {
        rrr.push([]);
        var ward = this.targetText.split(" ")[i];
        for (let n = 0; n < ward.length; n++) {
          rrr[i].push({
            letter:ward[n],
            color:this.colors.Default
          })
        }
      }

      
      this.targetTextWithColors = rrr
    },
    userTextChange(){
      var vm=this;
      vm.targetTextWithColors.forEach(ward => {
        ward.forEach(letter => {
          letter.color = this.colors.Default
        });
      });
      vm.check();
    },
    check(){
      var vm=this;

      for (let i = 0; i < vm.userText.split(" ").length; i++) {
        if(i >= vm.targetTextWithColors.length)continue
        let shift = 0;
        for (let n = 0; n < vm.userText.split(" ")[i].length; n++) {
          if(n >= vm.targetTextWithColors[i].length)continue

          let TTWC = vm.targetTextWithColors[i];

          if(vm.userText.split(" ")[i][n] != TTWC[n + shift].letter){
            if(TTWC[n + 1 + shift] && vm.userText.split(" ")[i][n] == TTWC[n + 1 + shift].letter && TTWC[n + 1 + shift].letter != " "){
              TTWC[n + shift].color = this.colors.Skipping
              shift += 1;
            }
          }
          TTWC[n + shift] = vm.targetTextWithColors[i][n + shift];

          if(vm.userText.split(" ")[i][n] == TTWC[n + shift].letter){
            TTWC[n + shift].color = this.colors.Right
          }else{
            TTWC[n + shift].color = this.colors.Wrong
          }
        }
      }

      this.chackIfDone()
    },
    chackIfDone(){
      var done=true;
      this.targetTextWithColors.forEach(ward => {
        ward.forEach(letter => {
          if(letter.color == this.colors.Default)done = false
        });
      });

      if(done){
        console.log("done");
      }
    }
  },
  computed: {
    targetTextShow(){
      var letters = [];
      this.targetTextWithColors.forEach(ward => {
        ward.forEach(letter => {
          letters.push(letter)
        });
        letters.push({letter:" ",color:this.colors.Default})
      });
      
      return letters;
    }
  },
  watch: {
    userText(){
      this.userTextChange()
    }
  },
}
</script>

